<template>
  <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="商品名称">
            <span>{{ props.row.name }}</span>
          </el-form-item>
          <el-form-item label="所属店铺">
            <span>{{ props.row.shop }}</span>
          </el-form-item>
          <el-form-item label="商品 ID">
            <span>{{ props.row.id }}</span>
          </el-form-item>
          <el-form-item label="店铺 ID">
            <span>{{ props.row.shopId }}</span>
          </el-form-item>
          <el-form-item label="商品分类">
            <span>{{ props.row.category }}</span>
          </el-form-item>
          <el-form-item label="店铺地址">
            <span>{{ props.row.address }}</span>
          </el-form-item>
          <el-form-item label="商品描述">
            <span>{{ props.row.desc }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column 
      sortable
      label="商品 ID"
      prop="id">
    </el-table-column>
    <el-table-column
      sortable
      label="商品名称"
      prop="name">
    </el-table-column>
    <el-table-column
      sortable
      label="描述"
      prop="desc">
    </el-table-column>
  </el-table>
</template>

<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>

<script>
let data = [];
for (var i = 1; i <= 10; i++) {
  data.push({
    id: i,
    name: "名称-" + i,
    category: "种类-" + i,
    address: "地址-" + i,
    shop: "商店-" + i,
    desc: "描述-" + i,
    shopId: "商店id-" + i
  });
}
export default {
  data() {
    return {
      tableData: data
    };
  }
};
</script>


